.dropdown {
  position: relative; 
}

.dropdown-button {
  padding: 30px 50px;
  font-size: 16px;
  cursor: pointer;
  background-color: #E5EAE6;
  border: 1px solid #cac8c8;
  border-radius: 8px;
  font-family: "Poppins", serif;
  font-weight: 200;
  font-style: normal;
}

.dropdown-menu {
  position: absolute;
  top: 100%; 
  left: 0;
  background-color: #E5EAE6;
  border: 1px solid #e2e0e0;
  list-style: none;
  padding: 10px;
  margin: 0;
  display: none; /* Oculta o menu inicialmente */
  font-size: 12px;
  font-family: "Poppins", serif;
  font-weight: 200;
  font-style: normal;
}

.dropdown-menu li {
  padding: 5px 0;
}

.dropdown-menu li:hover {
  background-color: #f0f0f0;
}

/* Exibe o menu quando o estado estiver ativo */
.dropdown.open .dropdown-menu {
  display: block;
}

/* Estilo geral da página */
.page-container {
    width: 100%;
    height: auto;
    background-image: url(/static/media/reservas.8994196c.jpg);
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: flex-end; 
    padding: 20px;
    position: relative;
  }
  
  /* Formulário */
  .form-container {
    background-color: #E5EAE6; 
    border-radius: 8px;
    padding: 20px;
    width: 400px;
    margin-right: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    margin-top: 40px;
    margin-bottom: 20px;
  }
  
  .form-container h1 {
    font-family: "Poppins", serif;
    font-weight: 500;
    font-size: 24px;
    margin-bottom: 3px;
    padding-bottom: 3px;
    margin-top: 5px;
    text-align: center;
  }
  
  .form-container p {
    font-weight: 300;
    font-size: 13px;
    text-align: center;
    margin-bottom: 20px;
    margin-top: 5px;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  .form-container input,
  .form-container textarea {
    width: calc(100% - 20px);
    padding: 10px;
    border-radius: 8px;
    border: 1px solid #ddd;
  }
  
  .form-container button {
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    border-radius: 8px;
    border: none;
    background-color: #ffa07a;
    color: white;
    font-family: "Poppins", serif;
    font-weight: 500;
    cursor: pointer;
  }
  
  .form-container button:hover {
    background-color: #ff6347;
  }
  
